.card {
  width: $card-width;
  height: $card-height;
  background: $card-background-color;
  border-radius: $card-radius;
  border: 1px solid $card-grey-elements;

&__bottom {
  padding-inline: $card-bottom-padding-inline;
}

&__image {
  display: block;
  width: 160px;
  height: 134px;
  padding: 32px 19px 40px;
}

&__id {
  margin: 0;
  padding-bottom: 16px;

  color: $card-secondary-accent;
  font-family: inherit;
  font-size: 10px;
  line-height: 14px;
}

&__title {
  margin: 0;
  padding-bottom: 4px;

  color: $card-main-accent;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
}

&__review {
  display: flex;
  justify-content: space-between;
  align-self: center;
  padding-bottom: 24px;

  &-count {
    margin: 0;
    font-family: inherit;
    color: $card-main-accent;
    font-size: 10px;
    line-height: 14px;
  }
}

&__price {
  display: flex;
  justify-content: space-between;
  padding-bottom: 16px;

&-title {
  margin: 0;
  color: $card-secondary-accent;
  font-family: inherit;
  font-size: 12px;
  line-height: 18px;
}

&-cost {
  margin: 0;
  color: $card-main-accent;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  }
}

&__button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-bottom: 16px;
  background-color: $card-blue-accent;
  border-radius: $card-radius;

  color: #fff;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-transform: uppercase;

  &:hover {
    background-color: #fff;
    border: 1px solid $card-blue-accent;
    color: $card-blue-accent;
  }
 }
}